<template>
  <div id="nav">
    <router-link :to="`/${$i18n.locale}`" exact>
      <a>
        <div class="logo">
          <img src="../images/logo.svg" alt="Logo Image" />
        </div>
      </a>
    </router-link>
    <ul class="navbar">
      <router-link :to="`/${$i18n.locale}`" tag="li">
        <a id="nav-link">
          <span class="underline--magical">{{ $t('nav.home')}}</span>
        </a>
      </router-link>
      <router-link :to="`/${$i18n.locale}/projectpage`" tag="li" active-class="active">
        <a id="nav-link">
          <span class="underline--magical">{{ $t('nav.projects')}}</span>
        </a>
      </router-link>
      <router-link :to="`/${$i18n.locale}/mycv`" tag="li" active-class="active">
        <a id="nav-link">
          <span class="underline--magical">{{ $t('nav.resume')}}</span>
        </a>
      </router-link>
      <router-link :to="`/${$i18n.locale}/contactme`" tag="li" active-class="active">
        <a id="nav-link">
          <span class="underline--magical">{{ $t('nav.contact')}}</span>
        </a>
      </router-link>
      <li>
        <div class="lang-select">
          <div class="lang-button" @click="opened = !opened">
            <img :src="require(`../images/flag_${$i18n.locale}.png`)" alt />
          </div>
          <ul v-show="opened" v-click-outside="hide" class="lang-list">
            <li class="flagc" @click.prevent="setLocale('en'), hide()">
              <img class="flag" src="../images/flag_en.png" alt />
            </li>
            <li class="flagc" @click.prevent="setLocale('ja'), hide()">
              <img class="flag" src="../images/flag_ja.png" alt />
            </li>
            <li class="flagc" @click.prevent="setLocale('tr'), hide()">
              <img class="flag" src="../images/flag_tr.png" alt />
            </li>
          </ul>
        </div>
      </li>
    </ul>
  </div>
</template>


<script>
import ClickOutside from "vue-click-outside";

export default {
  name: "nav-bar",
  data: function () {
    return {
      opened: false,
    };
  },
  methods: {
    setLocale(locale) {
      this.$root.$i18n.locale = locale;
      this.$router.push({
        params: { lang: locale },
      });
    },
    hide() {
      this.opened = false;
    },
  },
  events: {
    closeEvent: function () {
      this.hide();
    },
  },
  mounted() {
    // prevent click outside event with popupItem.
    this.popupItem = this.$el;
  },
  directives: {
    ClickOutside,
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/_variables.scss";

#nav {
  width: 100%;
  height: 12vh;
  border-bottom: 1px rgb(219, 217, 217) solid;
  .logo {
    position: absolute;
    left: 6vh;
    top: 10px;
    img {
      width: 10vh;
      &:hover {
        transform: scale(1.1);
        transition: all 300ms ease;
      }
    }
  }
  .underline--magical {
    background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
    background-repeat: no-repeat;
    background-size: 100% 0em;
    background-position: 0 88%;
    transition: background-size 0.15s ease-in;
    &:hover {
      background-size: 100% 100%;
    }
  }

  .navbar {
    position: absolute;
    right: 2vh;
    top: 1.5vh;
  }

  #nav-link {
    text-decoration: none;
    font-family: $p-font;
    font-weight: 700;
    font-size: 2.5vh;
    transition: all 0.2s ease-out;
    color: $p-color;
    display: flex;
    align-items: center;
    &:link {
      text-decoration: none;
    }

    &:visited {
      text-decoration: none;
    }
  }
  .active {
    a {
      text-decoration: underline !important;
    }
  }
  ul {
    display: flex;
    list-style: none;
    align-items: center;

    li {
      margin-left: 2.5vh;
      &:last-child {
        margin-left: none;
      }
    }
  }

  .lang-select {
    display: flex;
    justify-content: center;
    align-content: center;
    width: 5.5vh;
    .lang-button {
      background: none;
      border: none;
      border-radius: 6px;
      cursor: pointer;
      transition: all 200ms;
      width: inherit;
      &:hover {
        transform: scale(1.1);
      }
      img {
        width: inherit;
      }
    }
  }
  .lang-list {
    width: 7vh;
    display: flex;
    flex-direction: column;
    position: absolute;
    justify-content: center;
    align-items: center;
    top: 6vh;
    right: -6px;
    padding: 0;
    margin: 0;
    border-top: 2px solid #000;
    background-color: white;
    border: 1px solid #888;
    border-radius: 5px;
    box-shadow: 0px 4px 4px 0px rgb(197, 196, 196);

    .flagc {
      width: inherit;
      padding: 0;
      margin-right: 7px;
      img {
        width: 5.5vh;
        margin: 0 auto;
        cursor: pointer;
        &:hover {
          transform: scale(1.1);
        }
      }
    }
  }

  /* MEDIA QURIES */

  @media #{$large_mobile, $sm_mobile} {
    border-bottom: none;
    .logo {
      img {
        width: 8vh;
        position: absolute;
        left: -10px;
        top: 5px;
      }
    }
    #nav-link {
      display: none;
    }
    .lang-select {
      position: absolute;
      right: 20px;
    }
  }
  //Dark mode
 /*  @media (prefers-color-scheme: dark) {
    .underline--magical {
          background-image: linear-gradient(120deg, #84faaf5e 0%, #8fd2f469 100%);

    }
  } */
}
</style> 

